/**
 * Card design
 */
.card {
  background-color: #fff;
  outline: 1px solid $light-grey;
  box-shadow: 0 5px 6px rgba(0,0,0,0.20);
  border-radius: 2px;

  .card__header {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 20px;
    border-bottom: 1px solid $light-grey;
    text-transform: capitalize;

    i, h2 {
      font-size: $card-heading-size;
    }

    i {
      margin-right: 20px;
      color: $mid-grey;
    }

  }

  .card__content {
    padding: 25px;

    a {
      color: $blue;
    }
  }
}
